.footer {
  background-color: var(--color-bg);
}

.footer-container {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 12px;
  padding-top: 28px;
  padding-bottom: 28px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;

  @include tablet {
    flex-direction: row;
    gap: 0;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;
    font-size: 16px;
    line-height: 1.187;
  }
  @include desktop {
    padding-top: 29px;
    padding-bottom: 29px;
  }
}

.footer__text {
  margin-right: 5px;
}

.footer__icon {
  margin-left: 10px;
  margin-right: 10px;
  animation: heartbeat 1s infinite;
}

.footer__link {
  text-decoration: underline;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  20% {
    transform: scale(1.25);
  }
  40% {
    transform: scale(1.5);
  }
}

.footer__backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  opacity: 1;
  top: 0;
  position: fixed;
}

.footer__backdrop.is-hidden {
  opacity: 0;
  -webkit-transition: opacity var(--animation);
  transition: opacity var(--animation);
  pointer-events: none;
}

.footer__modal {
  height: 90vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  z-index: 1000;
  background-color: var(--color-bg);
  padding: 20px;
  width: calc(100% - 40px);
  overflow-y: auto;
  text-align: center;
}

.modal__button-close {
  position: absolute;
  z-index: 1;
  width: 25px;
  height: 25px;
  top: 20px;
  right: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: 50%;
  outline: none;
  box-shadow: -2px -2px 5px #fff, 3px 3px 5px rgba(0, 0, 0, 0.1);
  @include mobile {
    right: 30px;
  }
}
.modal__icon-close {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__icon-close:focus,
.modal__icon-close:hover {
  outline: none;
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.3);
  border-radius: 50%;
}

.modal__cross {
  display: inline;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.team {
  position: relative;
  text-align: center;
  margin-bottom: 20px;
  padding-right: 140px;
  color: var(--color-black);
  @include mobile {
    padding-right: 160px;
  }
}

.modal__container-list {
  margin-top: -20px;
  padding-left: 100px;
  text-align: left;
  list-style: none;

  -webkit-animation-name: change;
  -webkit-animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
  animation-name: change;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  @include mobile {
    padding-left: 150px;
  }
}
.modal__container-list-item {
  line-height: 40px;
  margin: 0;
  color: var(--color-accent);
}

.team__foto-img {
  border-radius: 25px;
  width: 250px;
  height: 200px;
  padding-top: 15px;
}
.team__wrapper {
  transition: 0.5s;
  cursor: pointer;
  border-radius: 15%;
}
.team__wrapper:hover {
  transform: scale(1.05);
}
//  box-shadow: 10px 10px 15px rgba(0,0,0,0.3);}

.team__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin-top: 60px;
  gap: 10px;
}

.team__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  border-radius: 15px;
  margin-bottom: 20px;
  //

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    width: 100%;
    flex-basis: calc((100% - 1 * 32px) / 2);
    margin-right: 5px;
    margin-bottom: 15px;
    &:not(:last-child) {
      margin-bottom: 15px;
      margin-right: 5px;
    }

    &:nth-child(2n) {
      margin-right: 0;
    }

    &:last-child {
      margin-right: auto;
      margin-bottom: 0px;
    }

    &:nth-last-child(-n + 2) {
      margin-bottom: 0;
    }
  }

  @media screen and (min-width: 1200px) {
    flex-basis: calc((100% - 3 * 16px) / 4);
    margin-right: 5px;
    margin-bottom: 15px;

    &:nth-child(4n) {
      margin-right: 0;
    }

    &:last-child {
      margin-right: auto;
      margin-bottom: 0px;
    }

    &:nth-last-child(-n + 4) {
      margin-bottom: 0;
    }
  }

  // transition-property: scale;
  // transition-duration: 500ms;
  // transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:active,
  &:focus {
    scale: 1.03;
  }
}

.team__student-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.team__student-name {
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.5;
  letter-spacing: 0.05em;
  transition: 1s;
  cursor: pointer;
  color: var(--color-black);
}

.team__student-role {
  font-size: 16px;
  line-height: 1;
  color: var(--color-accent);
  margin-bottom: 4px;
}
.sns-list__link {
  margin-bottom: 4px;
}

.sns-list__icon:hover,
.sns-list__icon:focus {
  fill: var(--color-accent);
}

.footer__link {
  cursor: pointer;
}

.team__wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.team__wrapper::before,
.team__wrapper::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: scale3d(0, 0, 1);
  transition: transform 0.3s ease-out 0s;
  background: rgba(255, 255, 255, 0.1);
  content: '';
  pointer-events: none;
}

.team__wrapper::before {
  transform-origin: left top;
}
.team__wrapper::after {
  transform-origin: right bottom;
}
.team__wrapper:hover::before,
.team__wrapper:hover::after,
.team__wrapper:focus::before,
.team__wrapper:focus::after {
  transform: scale3d(1, 1, 1);
}

.modal__content {
  position: absolute;
  margin-top: 15px;
  left: 47%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  font-size: 20px;
  line-height: 40px;
  color: #ecf0f1;
  @include mobile {
    left: 50%;
    font-size: 25px;
  }
}

.modal__content-container {
  font-weight: 600;
  overflow: hidden;
  height: 40px;
  padding: 0 10px;
  @include mobile {
    padding: 0 40px;
  }
  &:before {
    content: '[';
    left: 0;
  }

  &:after {
    content: ']';
    position: absolute;
    right: 0;
  }

  &:after,
  &:before {
    position: absolute;
    top: 0;
    color: var(--color-accent);
    font-size: 42px;
    line-height: 40px;

    -webkit-animation-name: opacity;
    -webkit-animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
    animation-name: opacity;
    animation-duration: 2s;
    animation-iteration-count: infinite;
  }
}

@-webkit-keyframes opacity {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@-webkit-keyframes change {
  0%,
  12.66%,
  100% {
    transform: translate3d(0, 0, 0);
  }
  16.66%,
  29.32% {
    transform: translate3d(0, -25%, 0);
  }
  33.32%,
  45.98% {
    transform: translate3d(0, -50%, 0);
  }
  49.98%,
  62.64% {
    transform: translate3d(0, -75%, 0);
  }
  66.64%,
  79.3% {
    transform: translate3d(0, -50%, 0);
  }
  83.3%,
  95.96% {
    transform: translate3d(0, -25%, 0);
  }
}

