.memory-game {
  margin-top: 50px;
}
.memory-game__list {
  width: 220px;
  height: 200px;
  perspective: 1000px;
  flex-wrap: wrap;
  margin: auto;
  display: flex;
  margin-top: 10px;
  margin-bottom: 20px;
  @include mobile {
    width: 370px;
    height: 280px;
  }
  @include tablet {
    width: 550px;
    height: 500px;
  }
}
.memory-game__title {
  margin-top: 10px;
}
.memory-card {
  width: calc(25% - 10px);
  height: calc(33.333% - 10px);
  margin: 5px;
  position: relative;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  transform: scale(1);
  transform-style: preserve-3d;
  transition: transform 0.5s;
}

.memory-card:active {
  transform: scale(0.97);
  transition: transform 0.2s;
}
.memory-card.flip {
  transform: rotateY(180deg);
}

.front-face,
.back-face {
  width: 100%;
  height: 100%;
  padding: 5px;
  position: absolute;
  border-radius: 5px;
  background: #da8281cc;
  backface-visibility: hidden;
  object-fit: contain;
}
.front-face {
  transform: rotateY(180deg);
}
